                                <div :class="`progress-bar w-${hero.powerstats.intelligence}`">
                                </div>
                            </div>
                            <div class="skill-name">
                                <p>combat</p><p>{{hero.powerstats.combat}}%</p>
                            </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.combat}`">
                                </div>
                            </div><div class="skill-name">
                            <p>durability</p><p>{{hero.powerstats.durability}}%</p>
                        </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.durability}`">
                                </div>
                            </div><div class="skill-name">
                            <p>Power</p><p>{{hero.powerstats.power}}%</p>
                        </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.power}`">
                                </div>
