<script>
    import {access_token,gid} from "@/superhero";
    import axios from "axios";


    export default {
        data() {
            return {
                heroes: [],
            }
        },
        mounted() {
            this.getcharecters()
        },
        methods: {
            getcharecters: function () {
                gid.forEach((id) => {
                    console.log(id)
                })
                this.heroes.splice(0)    // avoiding huge number of search results
                gid.forEach((id) => {
                    axios.get(`https://superheroapi.com/api.php/${access_token}/${id}`).then((result) => {
                        this.heroes.push(result.data)
                        console.log(result)
                    }).catch((error) => console.log(error))
                })
            }
        },
    }
</script>
